<template>
  <div class="hello">
    <div class="container">
      <div class="row">
        <div class="col-sm-12 col-xs-12">
          <table class="table table-bordered">
            <thead>
            <tr>
              <th>Дисциплина</th>
              <th>Вид</th>
              <th>Препод</th>
              <th>Время</th>
              <th>Аудитория</th>
            </tr>
            </thead>
            <tbody v-for="(numLession, index, num) in days" :key="index">
            <tr>
              <td class="active" colspan="6">{{ wd[num] }}</td>
            </tr>
            <tr v-for="(lession, index) in numLession" :key="index">
              <td v-for="(value, index) in lession" :key="index">{{ value }}</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import fullContentTable from './contentTable'
import {mapGetters} from 'vuex'

export default {
  name: 'fullTable',
  components: {fullContentTable},
  data () {
    return {
      wd: ['Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница']
    }
  },
  computed: {
    ...mapGetters({days: 'getFull'})
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .active{
    text-align: center;
    font-weight: 900;
    font-size: 24px;
  }
</style>



